body {
    background: url("../../../assets/images/concrete.png");
    background-size: auto, cover;
}

.navbar {
    background-color: white;
    margin-bottom: 35px;
    font-family: 'Roboto Mono', monospace;
}

#jacob {
    background-color: black;
    color: white;
    padding: 13px;
}

h1 {
    font-family: 'Roboto Mono', monospace;
    color: black;
}

.jumbotron {
    background: url("../../../assets/images/concrete.png");
    color: #777777;
    /* border: #dddddd solid 2px; */
    margin-bottom: 85px;
}

.row {
    font-family: 'Roboto Mono', monospace;
