}

.nav-item {
    font-family: 'Roboto Mono', monospace;
    color: #777777;
}

footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: black;
    color: white;
    text-align: center;
    padding: 15px;
    font-family: 'Roboto Mono', monospace;
}

#contactMe {
    text-align: center;
}

#information {
    font-size: 23px;
